<template>
  <div class="goods-seckill">
    <div class="banner">
      <!-- <van-swipers imgUrls="{{bannerList}}" height="284rpx" > </van-swipers> -->
      <ad-swiper :id="14" padding="10px" />
    </div>
    <div class="time-list-container row" ref="timeListContainer" style="touch-action: pan-y">
      <div class="time-list row">
        <div
          class="column-center time-item"
          v-for="(item, index) in seckillTime"
          :id="'item'+index"
          :key="index"
          @click="changeTimeList(index)"
        >
          <div class="xl bold" :class="{'primary': item.active == 1}">{{item.start_time}}</div>
          <div
            class="br60 xs normal"
            :class="{'bg-primary white': item.active == 1, 'muted': item.status == 2 }"
            style="padding: 0 5px"
          >{{item.tips}}</div>
        </div>
      </div>
    </div>
    <div class="goods-list mt5" v-show="!isDataNull">
        <van-list 
        v-model="loading" 
        :finished="isfinished" 
        finished-text="没有更多了" 
        @load="$getSeckillGoods">
          <div
            class="goods-item row bg-white"
            v-for="(item, index) in seckillGoods"
            :key="index"
            @click="goGoodsDetail(item.id, item.shop_id)"
          >
            <div class="goods-img mr10">
              <van-image width="100%" height="100%" radius="10px" :src="item.image" />
            </div>
            <div class="goods-info">
              <div class="goods-name two-txt-cut mb5 nr">{{item.name}}</div>
              <!-- <div class="progress-wrap">
                        <van-progress custom-class="progress" stroke-width="10" pivot-text="橙色" :color="primaryColor" track-color="#F2F2F2" percentage="25" show-pivot="{{false}}" />
                        <text class="xs ml10">已抢30%</text>
              </div>-->
              <label class="sale-info xs primary br60">已抢{{item.sales_sum}}件</label>
              <div class="info-footer row-between" style="margin-top: 2px">
                <div class="price">
                  <!-- <text class="primary mr10">
                                <text class="sm">￥</text>
                                <text class="xl">888.9</text>
                  </text>-->
                  <price-slice
                    class="mr5"
                    :price="item.seckill_price"
                    :color="primaryColor"
                    firstClass="xl"
                    secondClass="sm"
                    :showSubscript="true"
                    subScriptClass="sm"
                  ></price-slice>
                  <!-- <text class="muted xs line-through">¥3879</text> -->
                  <price-slice
                    class="line-through muted"
                    :price="item.min_price"
                    color="#999999"
                    firstClass="xs"
                    secondClass="xs"
                    :showSubscript="true"
                    subscriptClass="xs"
                  ></price-slice>
                </div>
                <button
                  class="br60 white nr"
                  :class="{'gray': currentStatus == 2, 'primary-btn': currentStatus == 1,'boder-btn primary': currentStatus == 0}"
                  size="sm"
                  @click.stop="goGoodsDetail(item.id, item.shop_id)"
                >
                {{currentStatus == 2 ? '已结束': currentStatus == 1 ? '立即抢购' : '未开始'}}
                </button>
              </div>
            </div>
          </div>
        </van-list>
    </div>
    <div class="data-null column-center" v-show="isDataNull">
      <div class="img-null">
        <img src="@A/images/goods_null.png"/>
      </div>
      <div class="muted nr">
        暂无任何活动商品~
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "@better-scroll/core";
import { getSeckillTime, getSeckillGoods } from "@API/store";
import AdSwiper from "@C/AdSwiper";
export default {
  name: "goodsSeckill",
  components: {
    AdSwiper
  },
  data() {
    return {
      seckillTime: [],
      seckillGoods: [],
      loading: true,
      isfinished: false,
      page: 1,
      refreshing: false,
      id: 0,
      timeIndex: 0,
      timeStatus: [],
      timeScroll: null,
      currentStatus: 0,
      isDataNull: false,
    };
  },

  watch: {
  },

  methods: {
    goGoodsDetail(id) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: id},
      });
    },
    cleanStatus() {
      this.page = 1;
      this.isfinished = false;
      this.seckillGoods = [];
      this.isDataNull = false;
    },
    onRefresh() {
      this.cleanStatus();
      this.$getSeckillGoods();
    },
    changeTimeList(index) {
      // 获取当前时间段id
      let { id } = this.seckillTime[index];

      this.seckillTime.forEach((item, idx) => {
        if(index == idx) {
            item.active = 1;
            this.currentStatus = item.status;
        }
        else {
            item.active = 0;
        }
      });
      // 清除状态
      this.cleanStatus();
      this.id = id;
      this.$getSeckillGoods();
      this.timeIndex = index - 2;
      if(this.timeIndex < 0) {
        this.timeIndex = 0
      }
      this.timeScroll.scrollToElement("#item" + this.timeIndex, 500)
    },

    $getSeckillTime() {
      getSeckillTime().then((res) => {
        if (res.code == 1) {
          if(res.data.length && res.data.length <= 0) {
            this.isDataNull = true;
            return;
          }
          let {timeListContainer} = this.$refs;
          let index = res.data.findIndex(item => item.status == 1);
          if(index == -1) {
            index = res.data.findIndex(item => item.status == 0)
          }
          if(index == -1) {
            index = 0
          }
          else {
            res.data[index].active = 1;
          }
          this.id = res.data[index].id;
          this.seckillTime = res.data;
          this.currentStatus = res.data[index].status;
          this.$getSeckillGoods();
          this.$nextTick(() => {
            this.timeScroll = new BScroll(timeListContainer, {
              scrollX: true,
              eventPassthrough: "vertical",
              useTransition: false, 
            });
            this.timeIndex = index - 2;
            if(this.timeIndex < 0) {
              this.timeIndex = 0;
            }
            this.timeScroll.scrollToElement("#item" + this.timeIndex)
          });
        }
      });
    },
    $getSeckillGoods() {
      this.refreshing = true;
      this.loading = true;
      if (this.isfinished == true) {
        this.refreshing = false;
        return;
      }
      getSeckillGoods({ id: this.id, page_no: this.page }).then((res) => {
        if (res.code == 1) {
          let { list, more } = res.data;
          this.loading = false;
          this.seckillGoods.push(...list);
          this.page ++;
          if (!more) {
            this.isfinished = true;
            this.refreshing = false;
          }
          if (this.seckillGoods.length <= 0) {
            this.isfinished = true;
            this.isDataNull = true;
            this.refreshing = false;
          }
        }
      });
    },
  },
  created() {
    this.$getSeckillTime();
  },
};
</script>

